* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 85%;
  margin: 0 auto;
  padding: 0 20px;
}

.search-form {
  width: 100%;
  justify-content: center;
  padding: 15px;
  display: flex;
}

.search-input {
  width: 300px;
  font: inherit;
  background-color: #f8f8f8;
  border: 1px solid;
  border-radius: 4px;
  padding: 8px 10px;
}

.form-btn {
  width: 50px;
  cursor: pointer;
  background-color: #c8c8c8;
  border: 1px solid;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  display: flex;
}

.form-btn:hover, .form-btn:focus {
  background-color: #b4b4b4;
  transition: all .3s;
}

.gallery {
  flex-wrap: wrap;
  margin: -15px;
  display: flex;
}

.gallery__link {
  width: calc(25% - 30px);
  border: 2px solid #f1eeee;
  border-radius: 10px;
  margin: 15px;
  transition: all .3s;
  overflow: hidden;
}

.gallery__link:hover, .gallery__link:focus {
  transform: scale(1.05);
}

.gallery-item {
  width: 100%;
  height: 80%;
  display: block;
}

.gallery-item__img {
  width: 100%;
  height: 100%;
  display: block;
}

.info {
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.info-item {
  color: #000;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more {
  font: inherit;
  color: #000;
  cursor: pointer;
  background-color: #fbfbfb;
  border: none;
  border-radius: 5px;
  margin: 25px auto 0;
  padding: 8px 20px;
  display: block;
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 2px 6px 2px rgba(60, 64, 67, .15);
}

.scroll-btn {
  width: 60px;
  height: 60px;
  z-index: 3;
  color: #90b0e8;
  border-radius: 50%;
  justify-content: center;
  align-items: flex-end;
  font-size: 40px;
  display: flex;
  position: fixed;
  bottom: 60px;
  right: 30px;
}

.load-more:hover, .scroll-btn:hover {
  background-color: #c3c3c3;
}

